.footer {
    background-color: #ffffff;
    border-top: 1px solid #f0eae4;
    &__inner {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        box-sizing: border-box;
        padding-top: 45px;
        padding-bottom: 78px; }
    &__head {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: flex-end;
        max-width: 665px;
        width: 100%; }
    &__logo {
        width: 240px;
        height: 39px;
        background: url('/img/logo.png') no-repeat 50% 50%;
        background-size: contain;
        &_dark {
            background: url('/img/logo-dark.png') no-repeat 50% 50%; } }
    &__social-list {
        width: 212px;
        display: flex;
        justify-content: space-between; }
    &__social-icon {
        display: flex;
        width: 32px;
        height: 32px;
        background: #f0eae4;
        &_vk {
            background: url('/img/vk.svg') no-repeat 50% 50%;
            &:hover {
                background: url('/img/vk-hover.svg') no-repeat 50% 50%; } }
        &_inst {
            background: url('/img/inst.svg') no-repeat 50% 50%;
            &:hover {
                background: url('/img/inst-hover.svg') no-repeat 50% 50%; } }
        &_fb {
            background: url('/img/fb.svg') no-repeat 50% 50%;
            &:hover {
                background: url('/img/fb-hover.svg') no-repeat 50% 50%; } }
        &_ok {
            background: url('/img/ok.svg') no-repeat 50% 50%;
            &:hover {
                background: url('/img/ok-hover.svg') no-repeat 50% 50%; } }
        &_tw {
            background: url('/img/tw.svg') no-repeat 50% 50%;
            &:hover {
                background: url('/img/tw-hover.svg') no-repeat 50% 50%; } } }
    &__body {
        margin-top: 42px;
        display: flex;
        width: 100%; }
    &__lists {
        margin-right: 70px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        box-sizing: border-box;
        justify-content: space-between;
        align-content: space-between;
        height: 322px;
        width: 370px; }
    &__list {
        width: 170px;
        &_address {
            width: 231px; } }
    &__list-title {
        font-size: 18px;
        line-height: 1.33;
        color: #644733; }
    &__ul {
        margin-top: 10px;
        &_address {
            margin-top: 14px; }
        a {
            display: block;
            font-size: 16px;
            line-height: 1.25;
            text-decoration: none;
            color: #a2a2a1;
            margin-bottom: 4px; } }
    &__li {
        &_address {
            margin-bottom: 8px; } }
    &__address {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        justify-content: space-between;
        align-content: space-between;
        height: 318px;
        width: 486px; }
    &__search {
        position: relative;
        &:hover {
            .footer__search-input {
                border: solid 1px #ded3c8;
                &::placeholder {
                    color: #3e2311; } }
            .footer__search-icon {
                background: url('/img/search-hover.svg') no-repeat 50% 50%; } } }
    &__search-input {
        padding-left: 46px;
        position: relative;
        width: 430px;
        height: 45px;
        border-radius: 5px;
        border: solid 1px #f0eae4;
        font-size: 16px;
        &::placeholder {
            font-size: 16px;
            color: #b3b3b3; }
        &:hover {
            border: solid 1px #ded3c8;
            &::placeholder {
                color: #3e2311; } } }


    &__search-icon {
        position: absolute;
        content: '';
        display: block;
        width: 29px;
        height: 29px;
        top: 8px;
        left: 10px;
        background: url('/img/search.svg') no-repeat 50% 50%;
        z-index: 10;
        cursor: pointer; }
    &__gift {
        position: absolute;
        right: 26px;
        top: 40px;
        text-decoration: none; }
    &__gift-img {
        width: 143px;
        height: 61px;
        background: url('https://via.placeholder.com/143x61.png') no-repeat 50% 50%; }
    &__gift-text {
        margin-top: 30px;
        margin-left: 13px;
        font-size: 16px;
        line-height: 1.25;
        color: #a2a2a1; }
    &__copyright {
        padding: 25px 0;
        box-sizing: border-box;
        background-color: #f0eae4;
        font-size: 16px;
        line-height: 1.25;
        color: #a2a2a1; }
    &__accordion {
        display: none;
        margin-top: 45px;
        width: 240px; } }



@media (max-width: 1024px) {
    .footer {
        &__inner {
            padding-top: 60px;
            padding-bottom: 35px; }
        &__head {
            max-width: 568px;
            width: 70%; }
        &__body {
            margin-top: 34px; }
        &__lists {
            margin-right: 0;
            height: 271px;
            width: 341px; }
        &__list {
            width: 170px;
            &_address {
                width: 200px; } }
        &__list-title {
            font-size: 16px; }
        &__li {
            font-size: 14px;
            margin-bottom: 3px;
            &_address {
                margin-bottom: 7px; } }
        &__address {
            height: 267px;
            width: 456px; }
        &__gift {
            right: 65px;
            top: 50px; }
        &__gift-text {
            margin-top: 4px;
            font-size: 14px; }
        &__copyright {
            font-size: 14px; } } }

@media (max-width: 768px) {
    .footer {
        &__inner {
            margin: 0 auto;
            max-width: 528px; }
        &__head {
            max-width: 514px;
            width: 100%; }
        &__body {
            flex-direction: column;
            margin-top: 38px; }
        &__lists {
            height: 320px;
            max-width: 447px;
            width: 100%;
            margin-right: 0; }
        &__list {
            width: 161px;
            &_address {
                max-width: 231px;
                width: 48%; } }
        &__list-title {
            font-size: 18px; }
        &__li {
            font-size: 16px;
            margin-bottom: 4px;
            &_address {
                margin-bottom: 9px; } }
        &__address {
            margin-top: 36px;
            height: auto;
            max-width: 518px;
            width: 100%; }
        &__search {
            margin-top: 38px;
            width: 100%; }
        &__search-input {
            width: 100%; }
        &__gift {
            position: static;
            margin-top: 42px; }
        &__gift-text {
            margin-left: 13px; } } }
@media (max-width: 320px) {
    .footer {
        &__inner {
            padding: 35px 30px 70px;
            align-items: center;
            flex-direction: column; }
        &__head {
            flex-direction: column;
            align-items: center; }
        &__social-list {
            margin-top: 27px; }
        &__body {
            display: none; }
        &__accordion {
            display: block; }
        &__search {
            width: 240px;
            margin-top: 19px; }
        &__gift {
            margin-top: 84px; }
        &__copyright {
            padding: 18px 10px;
            font-size: 14px; } } }
