.catalog-tags {
    &__inner {
        padding: 15px 0; }
    &__title {
        font-size: 32px;
        line-height: 1.25;
        color: #5b4630;
        margin-bottom: 19px; }
    &__list {
        display: flex;
        flex-wrap: wrap; }
    &__button {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        margin-right: 8px;
        margin-bottom: 8px;
        padding: 4px 12px;
        font-size: 16px;
        color: #644733;
        border-radius: 14px;
        border: solid 1px #f0eae4;
        cursor: pointer;
        &:hover {
            border-color: #ded3c8; }
        &.active {
            border-color: #34ab9c;
            color: #ffffff;
            background-color: #34ab9c;
            &:hover {
                background-color: #169787;
                border-color: #169787; } } }

    &__img {
        display: none;
        box-sizing: border-box;
        width: 21px;
        height: 21px;
        border-radius: 2px;
        border: 1px solid #f0eae4; }

    input:checked ~ .catalog-tags__img {
        border: none;
        background: url('/img/select.svg') no-repeat 50% 50%;
        background-color: #f6773e; }

    &__mobile-menu {
        display: none;
        padding: 15px 0;
        box-sizing: border-box;
        width: 100%;
        align-items: center;
        border-bottom: 1px solid #f0eae4; }
    &__mobile-name {
        font-size: 14px;
        color: #b3b3b3; }
    &__mobile-text {
        margin-left: 5px;
        font-size: 14px;
        width: 210px;
        color: #644733;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; }
    &__mobile-button {
        width: 21px;
        height: 11px;
        background: url('/img/arrow-right.svg') no-repeat 50% 50%;
        margin-left: auto; }
    &__mobile-list {
        transition: all ease 0.3s; }
    &__mobile-header {
        width: 100%;
        display: none;
        justify-content: space-between;
        align-items: center; }
    &__mobile-header-title {
        font-size: 24px;
        line-height: 1.17;
        color: #5b4630; }
    &__mobile-header-close {
        width: 25px;
        height: 24px;
        background: url('/img/close-big.svg') no-repeat 50% 50%;
        background-size: contain; }
    &__menu-button {
        display: none;
        justify-content: center;
        align-items: center;
        position: sticky;
        left: 0;
        right: 0;
        bottom: 0;
        height: 60px;
        background-color: #f6773e;
        color: #ffffff;
        font-size: 16px;
        line-height: 1.5; } }

@media (max-width: 1024px) {
    .catalog-tags {} }



@media (max-width: 768px) {
    .catalog-tags {} }


@media (max-width: 320px) {
    .catalog-tags {
        position: relative;
        &::before {
            position: absolute;
            content: '';
            top: -25px;
            left: 50%;
            margin-left: -160px;
            width: 320px;
            height: 625px;
            background: url('/img/tags-bg.png') no-repeat 50% 0;
            z-index: -1; }

        &__title {
            font-size: 24px;
            line-height: 1.67;
            margin-bottom: 0; }
        &__inner {
            padding: 0; }
        &__mobile-menu {
            display: flex; }
        &__mobile-header {
            padding: 20px;
            display: flex; }
        &__mobile-list {
            width: 100%;
            height: 100vh;
            padding: 0;
            position: fixed;
            top: 0;
            left: -320px;
            background: #ffffff;
            z-index: 100;
            overflow-y: scroll;
            &.active {
                left: 0; } }
        &__list {
            padding: 20px;
            flex-direction: column; }

        &__button {
            padding: 14px 0;
            border: none;
            margin: 0;
            &.active {
                background: none;
                color: #644733;
                &:hover {
                    background-color: transparent;
                    border-color: transparent; } } }
        &__img {
            display: block; }
        &__menu-button {
            display: flex; } } }
