.catalog-filter {
    &__inner {
        display: flex;
        align-items: baseline;
        padding: 15px 0;
        flex-wrap: wrap; }
    &__target {
        display: flex;
        position: relative;
        align-items: baseline;
        margin-bottom: 30px; }
    &__name {
        font-size: 38px;
        color: #5b4630;
        white-space: nowrap; }
    &__button {
        margin: 0 15px;
        font-family: Madelyn;
        font-size: 85px;
        color: #5b4630;
        line-height: 26px;
        border-bottom: 2px solid #644733;
        cursor: pointer;
        white-space: nowrap;
        span {
            font-family: 'Caveat';
            font-size: 40px;
            color: #644733;
            font-weight: bold; } }
    &__menu {
        box-sizing: border-box;
        padding: 20px;
        display: none;
        // display: flex
        justify-content: space-between;
        position: absolute;
        z-index: 100;
        width: 770px;
        height: 386px;
        top: 75px;
        left: 0;
        border-radius: 5px;
        box-shadow: 0 10px 45px 0 rgba(0, 0, 0, 0.16);
        background-color: #ffffff;
        &.active {
            display: flex; } }
    &__menu-button {
        display: none;
        justify-content: center;
        align-items: center;
        position: sticky;
        left: 0;
        right: 0;
        bottom: 0;
        height: 60px;
        background-color: #f6773e;
        color: #ffffff;
        font-size: 16px;
        line-height: 1.5; }
    &__menu-ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: space-between; }
    &__menu-li {
        margin-bottom: 5px;
        padding: 5px 12px;
        height: 32px;
        border-radius: 7px;
        font-size: 16px;
        cursor: pointer;
        &.active {
            background-color: #f8f5f2;
            font-weight: bold; } }
    &__price {
        align-items: baseline;
        display: flex;
        position: relative; }
    &__reset {
        margin-left: auto;
        font-size: 16px;
        color: #b3b3b3;
        cursor: pointer;
        &:hover {
            color: #ee5f1f; } }
    &__mobile-menu {
        display: none;
        padding: 15px 0;
        box-sizing: border-box;
        width: 100%;
        align-items: center;
        border-bottom: 1px solid #f0eae4; }
    &__mobile-name {
        font-size: 14px;
        color: #b3b3b3; }
    &__mobile-text {
        margin-left: 5px;
        font-size: 14px;
        width: 210px;
        color: #644733;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; }
    &__mobile-button {
        width: 21px;
        height: 11px;
        background: url('/img/arrow-right.svg') no-repeat 50% 50%;
        margin-left: auto; }
    &__mobile-list {
        transition: all ease 0.3s; }
    &__mobile-header {
        padding: 20px;
        width: 100%;
        display: none;
        justify-content: space-between;
        align-items: center; }
    &__mobile-header-title {
        font-size: 24px;
        line-height: 1.17;
        color: #5b4630; }
    &__mobile-header-close {
        width: 25px;
        height: 24px;
        background: url('/img/close-big.svg') no-repeat 50% 50%;
        background-size: contain; } }
@media (max-width: 1024px) {
    .catalog-filter {
        &__name {
            font-size: 32px;
            line-height: 1.25; }
        &__button {
            font-size: 78px; } } }

@media (max-width: 768px) {
    .catalog-filter {
        &__inner {
            flex-wrap: wrap;
            justify-content: flex-start; }
        &__name {
            font-size: 28px;
            line-height: 1.43; }
        &__button {
            margin: 0 12px;
            font-size: 67px; }
        &__reset {
            margin-left: 0;
            margin-top: 25px; } } }
@media (max-width: 320px) {
    .catalog-filter {
        &__inner {
            padding: 0; }
        &__target {
            width: 100%; }
        &__price {
            width: 100%; }
        &__name {
            display: none; }
        &__button {
            display: none; }
        &__mobile-menu {
            display: flex; }
        &__mobile-header {
            display: flex; }
        &__mobile-list {
            width: 100%;
            height: 100vh;
            padding: 0;
            position: fixed;
            top: 0;
            left: -320px;
            background: #ffffff;
            z-index: 100;
            overflow-y: scroll;
            &.active {
                left: 0; } }
        &__mobile-text {
            width: auto; }
        &__menu {
            display: block;
            width: 100%;
            height: auto;
            box-shadow: none;
            padding: 0;
            flex-direction: column; }
        &__menu-ul {
            padding: 20px; }
        &__reset {
            margin-top: 15px;
            color: #f6773e;
            font-size: 14px; }
        &__menu-button {
            display: flex; } } }
