.index-cards {
    &__inner {
        display: flex;
        width: 100%; }
    &__item {
        position: relative;
        padding-top: 274px;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        width: 50%;
        height: 550px; }
    &__img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: url('https://via.placeholder.com/960x550.png') no-repeat 50% 50%;
        background-size: cover; }
    &__body {
        padding: 12px 20px;
        box-sizing: border-box;
        position: relative;
        display: flex;
        flex-direction: column;
        width: 368px;
        height: 179px;
        background-color: #492b26;
        z-index: 10; }
    &__name {
        font-size: 18px;
        line-height: 1.56;
        color: #ffffff; }
    &__text {
        margin-top: 5px;
        height: 83px;
        font-size: 12px;
        line-height: 1.42;
        color: #917f7c; }
    &__li {
        width: 100%; }
    &__price {
        height: 28px;
        font-size: 16px;
        line-height: 1.75;
        color: #ffffff; }
    &__button {
        position: absolute;
        display: flex;
        justify-content: center;
        right: -31px;
        bottom: -47px;
        padding-top: 7px;
        width: 301px;
        height: 85px;
        text-decoration: none;
        background: url('/img/sticker.svg') no-repeat 50% 50%;
        background-size: contain;
        span {
            transform: rotate(-3deg);
            font-family: Madelyn;
            font-size: 45px;
            line-height: 1.89;
            color: #5b4630; } }
    &__carousel {
        display: none !important;
        height: 569px; }
    &__dots {
        text-align: center;
        .owl-dot {
            margin-right: 10px;
            width: 10px;
            height: 10px;
            opacity: 0.4;
            border-radius: 5px;
            background-color: #3d2824 !important; }
        .active {
            width: 10px;
            height: 10px;
            opacity: 1;
            border-radius: 5px;
            background-color: #3d2824 !important; } } }


@media (max-width: 1024px) {
    .index-cards {} }


@media (max-width: 768px) {
    .index-cards {
        &__item {
            padding-top: 0;
            flex-direction: column;
            justify-content: flex-start;
            height: 528px;
            &:nth-child(2) {
                .index-cards__inner {
                    background-color: #3d2824; } } }
        &__img {
            position: static;
            width: 100%;
            height: 272px; }
        &__body {
            width: 100%;
            height: 256px; }
        &__button {
            right: 16px;
            bottom: 21px; } } }
@media (max-width: 320px) {
    .index-cards {
        &__inner {
            display: none; }
        &__carousel {
            display: block !important; }
        &__item {
            box-sizing: border-box;
            padding: 10px;
            width: 100%; }
        &__img {
            height: 200px; }
        &__body {
            height: 266px;
            padding: 12px 15px 50px; }
        &__text {
            height: 136px; }
        &__button {
            width: 281px;
            margin-right: -140px;
            right: 50%;
            bottom: -43px; } } }

